// src/controls/basicControls.js

/**
 * Inhouse (XML) verisindeki ADULTS + CHILDREN saysnn,
 * odada kaytl gerek isim saysyla tutup tutmadn kontrol eder.
 * * rnek: XML'de ADULTS="2" CHILDREN="1" yazyor ama isim listesinde 2 kii varsa hata verir.
 */
export const checkGuestCountConsistency = (inhouseData) => {
    const errors = [];

    inhouseData.forEach(room => {
        // XML'den gelen saysal veriler
        const adults = parseInt(room.adults || 0, 10);
        const children = parseInt(room.children|| 0, 10);
        const totalExpected = adults + children;

        // XML iindeki isimleri sayalm: Main Guest (1) + Accompanying (Varsa)
        let actualGuestCount = 0;
        
        
        
            actualGuestCount++;
        

        // Yanndakiler (Accompanying) stringini "/" iaretinden blp sayalm
        const accNames = room.accompanyNames;
        if (accNames && typeof accNames === 'string' && accNames.trim().length > 0) {
            const extraGuests = accNames.split('/').filter(name => name.trim().length > 0);
            actualGuestCount += extraGuests.length;
        }

        // Eer XML'deki say ile listedeki isim says tutmuyorsa
        if (actualGuestCount !== totalExpected) {
            errors.push({
                roomNo: room.roomNo,
                type: 'GUEST_COUNT_MISMATCH',
                message: `Kii Says Uyumsuz: XML'de ${totalExpected} kii (A:${adults} :${children}) grnyor, ancak ${actualGuestCount} isim kaytl.`,
                severity: 'warning'
            });
        }
    });

    return errors;
};

