// src/utils/checkRoutingComments.js

/**
 * Routing yaplan odalarn yorum (comment) ksmlarndaki misafir isimlerinin doruluunu kontrol eder.
 * @param {Array<Object>} inhouseData - lenmi Inhouse verisi (misafir ve oda bilgileri).
 * @param {Array<Object>} routingData - lenmi Routing verisi (hangi odalarn hangi odaya ynlendirildii).
 * @returns {Array<Object>} - Bulunan hatalar ieren bir dizi.
 */
export const checkRoutingComments = (inhouseData, routingData) => {
    const errors = [];

    // 1. Routing yaplan odalar ve ana oday tespit etme
    routingData.forEach(routingEntry => {
        const sourceRoom = routingEntry.SourceRoom;
        const targetRoom = routingEntry.TargetRoom;
        
        // Eer routing ilemi ana odaya (kendi kendine) yaplmsa atla.
        if (sourceRoom === targetRoom) {
            return;
        }

        // 2. Ynlendirilen odalardaki misafirlerin isimlerini alma
        const routedGuestNames = inhouseData
            .filter(room => room.roomNo === sourceRoom)
            .map(room => `${room.firstName} ${room.lastName}`);

        // Routing yaplan odada misafir yoksa devam et
        if (routedGuestNames.length === 0) {
            return;
        }
        
        // 3. Ana odann yorum (comment) ksmn inceleme
        const targetRoomData = inhouseData.find(room => room.roomNo === targetRoom);

        // Ana oda bulunamazsa veya yorumu yoksa hata ver.
        if (!targetRoomData) {
            errors.push({
                roomNo: targetRoom,
                message: `Ana oda (${targetRoom}) Inhouse verisinde bulunamad.`
            });
            return;
        }

        const comment = targetRoomData.comment || '';
        
        // Yorum 2 ile balamyorsa hata ver.
        if (!comment.startsWith('2')) {
            errors.push({
                roomNo: targetRoom,
                message: `Ynlendirme yaplan ana odann (${targetRoom}) yorumu '2' ile balamyor.`
            });
            return;
        }

        // Yorumdan isimleri ayklama (Burada daha karmak regex kullanlabilir ancak basit bir ayrtrma yapalm)
        const commentNames = comment.substring(1).trim().split(/, | ve /).filter(name => name.length > 0);

        // 4. Karlatrma
        const missingNamesInComment = routedGuestNames.filter(name => 
            !commentNames.some(commentName => 
                commentName.toLowerCase() === name.toLowerCase()
            )
        );

        if (missingNamesInComment.length > 0) {
            errors.push({
                roomNo: targetRoom,
                message: `Ana oda (${targetRoom}) yorumunda eksik veya hatal isimler var. Eksik isimler: ${missingNamesInComment.join(', ')}`,
                details: {
                    expected: routedGuestNames,
                    found: commentNames
                }
            });
        }
    });

    return errors;
};