// src/controls/rateControl.js

/**
 * Saysal deeri temizleyip karlatrma iin normalize eder.
 * @param {string|number} value 
 * @returns {string}
 */
const normalizePrice = (value) => {
  if (!value) return "";
  // Virglleri noktaya evir, rakam ve nokta dndaki her eyi sil
  return String(value)
      .replace(/,/g, '.') 
      .replace(/[^0-9.]/g, '');
};

/**
* Tablodaki rate deerinin comment iinde olup olmadn kontrol eder.
* Nokta/virgl farkllklarn ve binlik ayrclar tolere eder.
*/
const isRateInComment = (rate, comment) => {
  if (!rate || rate === 0 || rate === "0") return true; // Fiyat 0 ise kontrol etme
  if (!comment) return false;

  // 1. Rate'i temizle (rn: 3405.63)
  const cleanRate = normalizePrice(rate);
  const rateNumber = parseFloat(cleanRate);

  // 2. Comment iindeki saysal olabilecek her eyi yakala (3,405.60 gibi yaplar bozmadan)
  // Bu regex; rakam, nokta ve virgl ieren gruplar bulur.
  const potentialPrices = comment.match(/[0-9][0-9,.]*/g) || [];

  return potentialPrices.some(priceStr => {
      // Comment iindeki her bir say adayn normalize et
      // Eer say binlik ayrc (,) ieriyorsa onu temizleyip noktaya eviriyoruz
      const cleanPriceStr = priceStr.replace(/,/g, ''); 
      const commentPrice = parseFloat(cleanPriceStr);

      // Kuru farklarn tolere etmek iin (rn: 3405.60 vs 3405.63) 
      // Math.abs kullanarak aradaki fark 1 birimden azsa "tamam" diyoruz.
      // Eer kuruu kuruuna ayn olsun istersen: return commentPrice === rateNumber;
      return Math.abs(commentPrice - rateNumber) < 1; 
  });
};

export const getControlStyles = (rowData) => {
  const { rate, comment, caCl } = rowData;

  let highlightCompanyCell = false;
  let highlightRateCell = false;
  let highlightCommentCell = false;
  let highlightCaClCell = false;

  // --- 1. CA/CL KONTROL ---
  if (caCl === 'CL') {
      highlightCaClCell = true;
  }

  // --- 2. AKILLI RATE & COMMENT UYUMU ---
  // Artk "2 " kontrol yok. Tm odalar iin rate > 0 ise yorumda aranr.
  if (!isRateInComment(rate, comment)) {
      highlightRateCell = true;
  }

  // Not: Artk havayollar sabitleri kalkt iin highlightCompanyCell 
  // u anki mantkta false dnecektir. zel bir irket kural gelirse buraya eklenebilir.

  return {
      highlightRateCell,
      highlightCommentCell,
      highlightCaClCell,
      highlightCompanyCell
  };
};